@import '../../styles/mixin.scss';

.registrations {
    text-align: center;
    padding-top: 30px;

    @include phone() {
        padding: 10px;
    }
}

.photo_edit {
    width: 350px;
    margin: 0 auto;
    border-radius: 10px;
    overflow: hidden;
}

.logo {
    font-size: 28px;
    padding-bottom: 15px;
    display: block;
}

.and::before {
    content: '';
    display: block;
    width: 43%;
    height: 1px;
    background-color: rgba(170, 170, 170, 0.5);
    position: absolute;
    left: 0px;
    top: 0px;
    bottom: 0px;
    margin: auto;
}

.and {
    font-size: 15px;
    color: #aaa;
    margin: 10px 0px;
    position: relative;
}

.and::after {
    content: '';
    display: block;
    width: 43%;
    height: 1px;
    background-color: rgba(170, 170, 170, 0.5);
    position: absolute;
    right: 0px;
    top: 0px;
    bottom: 0px;
    margin: auto;
}

.avatar__edit {
    width: 100%;
    height: 100%;
    background-color: rgba(170, 170, 170, 0.6);
    position: absolute;
    z-index: 100;
    opacity: 0;
    transition: .5s;
}

.avatar__edit_img {
    width: 28px;
    height: 28px;
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    z-index: 1000;
    margin: auto;
    transition: .3s;

    &>svg {
        width: 28px;
        height: 28px;
        fill: black;
    }
}

.avatar__editor {
    width: 300px;
    margin: 0 auto;
    border-radius: 8px;
    overflow: hidden;
}

.avatar__img {
    width: 100%;
    height: 100%;
}

.avatar {
    position: relative;
    width: 60px;
    height: 60px;
    margin: 0 auto;
    border-radius: 50%;
    overflow: hidden;

    &:hover {
        .avatar__edit {
            opacity: 1;
        }

        .avatar__edit_img {
            top: 15px;
        }
    }
}