.overlay{
    position: fixed;
    z-index: 100;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;
    margin:  auto;
    background-color: rgba(0, 0, 0, 0.555);
    opacity: 0;
}
.overlay_animations{
    opacity: 1;
    transition: .2s;
}
.show{
    opacity: 0;
    background-color: transparent;
    transition: .2s;
}
.modal{
    display: flex;
    width: 90%;
    height: 90%;
    max-width: 1000px;
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    z-index: 100;
    margin: auto;
    background-color: #fff;
}

.modal_showModalIcon{
    position: absolute;
    right: 7px;
    top: 7px;
    width: 40px;
    height: 40px;
    z-index: 1000;
    & > img {
        width: 100%;
        height: 100%;
    }
}