.emailVerified {
    text-align: center;
}
.emailVerified__text{
    font-size: 17px;
    color: rgb(68, 68, 68);
}
.container {
    width: 100%;
    padding: 20px;
    max-width: 680px;
    margin: 0px auto;
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.btn{
    font-size: 16px;
    color:#fff;
    background-color: rgb(39, 32, 32);
    padding: 10px 14px;
    border: none;
    border-radius: 10px;
    margin: 0 auto;
    transition: .3s;
    cursor: pointer;
    &:hover{
        background-color: rgb(22, 19, 19);
    }
}