.error_text,
.ended_posts_text {
    font-size: 22px;
    text-align: center;
    padding: 20px 10px;
    font-weight: 600;
}


.modal_img_post_wrap {
    width: 60%;
    height: 100%;

    &>img {
        object-fit: contain;
        width: 100%;
        height: 100%;
    }
}

.modal_actions_wrap {
    border-left: 1px solid #aaa;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 40%;
    height: 100%;
}

.modal_avtorInfo {
    padding: 5px 10px;
    display: flex;
    align-items: center;

    &>img {
        width: 50px;
        height: 50px;
        object-fit: cover;
        border-radius: 50%;
        margin-right: 10px;
    }
}

.modal_actions {
    width: 100%;
}

.modal_comments {
    padding: 10px;
    border-top: 1px solid #aaa;
    border-bottom: 1px solid #aaa;
    width: 100%;
    height: 100%;
    overflow-y: scroll;
}

.modal_actions_icon {
    padding: 10px;
    display: flex;
    gap: 8px;
}

.modal_detailedCard__icons {
    font-size: 21px;
}

.modal_add_comment_wrap {
    border-top: 1px solid #aaa;
    width: 100%;
    padding: 8px 0px;
    position: relative;
    &>input {
        font-size: 16px;
        width: 100%;
        height: 100%;
        border: none;
        outline: none;
        padding: 8px 110px 8px 8px;
    }

    &>button {
        position: absolute;
        right: 5px;
        bottom: 0px;
        top: 0px;
        margin:  auto;
        background: transparent;
        border: none;
        font-weight: 700;
        font-size: 16px;
    }
}